.movie-backdrop,
.movie-poster {
  background: none;
  border: none;
}

.movie-poster img {
  width: 150px;
  height: 200px;
}

.movie-backdrop img {
  width: 250px;
  height: 150px;
}

.movie-poster img,
.movie-backdrop img {
  /* Nổi lên khi hover */
  transform: perspective(100px) translateZ(0px);
  transition: transform 100ms linear;
}

.movie-poster img:hover,
.movie-backdrop img:hover {
  /* Nổi lên khi hover */
  cursor: pointer;
  transform: perspective(100px) translateZ(5px);
}
