.nav-bar-container,
.nav-bar-scrolled {
  position: fixed;
  width: 100%;
  z-index: 10;
}

.nav-bar-container {
  background-color: transparent;
  transition: background-color 200ms linear;
}

.nav-bar-scrolled {
  background-color: #212529;
  transition: background-color 200ms linear;
}

.nav-bar {
  display: flex;
  justify-content: space-between;
  padding: 0 5%;
  top: 0;
}

.btn-header {
  color: #e03131;
  background: none;
  border: none;
  padding: 1.5rem 0;
  font-weight: 700;
  font-size: 20px;
}

.btn-header:hover {
  cursor: pointer;
}

.btn-search {
  background: none;
  border: none;
  transform: perspective(100px) translateZ(0px);
  transition: transform 100ms linear;
}

.btn-search:hover {
  cursor: pointer;
  transform: perspective(100px) translateZ(5px);
}

.icon {
  width: 24px;
  height: 24px;
}
