.detail-container {
  padding: 2rem 0;
  margin-top: 12px;
  display: grid;
  grid-template-columns: 2fr 2fr;
  padding-bottom: 24px;
  color: #f1f3f5;
  gap: 18px;
}

.detail-title {
  font-size: 28px;
}

.line {
  width: 100%;
  height: 2px;
  background-color: #f1f3f5;
}

.detail-release-date,
.detail-vote {
  font-weight: 700;
  font-size: 18px;
}

.detail-overview {
  line-height: 24px;
  font-size: 16px;
}

.btn-close {
  display: inline-block;
  margin-top: 24px;
  padding: 1rem 3rem;
  background-color: #f03e3e;
  border: none;
  color: #f1f3f5;
  font-size: 16px;
  letter-spacing: 0.5px;
}

.btn-close:hover {
  background-color: #f1f3f5;
  color: #f03e3e;
  cursor: pointer;
}

@media (max-width: 820px) {
  .detail-container {
    display: flex;
    flex-direction: column-reverse;
  }
}

@media (max-width: 576px) {
  .detail-container {
    padding-top: 0;
    gap: 0;
  }
  .btn-close {
    width: 100%;
  }
}
