.search-container {
  width: 100%;
  height: 50vh;
  background-color: #212529;
  position: relative;
}

.search {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -25%);
  background-color: #f1f3f5;
}

.search-input {
  width: 600px;
  height: 50px;
  border: none;
  padding-left: 1rem;
  border-bottom: 1px #e03131 solid;
  cursor: text;
  font-size: 16px;
  letter-spacing: 0.5px;
  position: relative;
}

.search-input:focus {
  outline: none;
}

.icon {
  position: absolute;
  width: 24px;
  height: 24px;
  transform: translate(-150%, 50%);
}

.btn-group {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding-right: 12px;
}

.btn {
  margin: 2rem 0;
  padding: 1rem 2rem;
  font-size: 16px;
}

.btn:hover {
  cursor: pointer;
}

.btn-reset {
  background-color: #f1f3f5;
  border: none;
}

.btn-search {
  background-color: #e03131;
  color: #f1f3f5;
  border: none;
}

@media (max-width: 576px) {
  .search-container {
    width: auto;
  }

  .search-input {
    width: auto;
  }
}
