.result-container {
  background-color: #212529;
  width: 100%;
  height: 100%;
  padding-top: 1rem;
}

.empty-result-container {
  background-color: #212529;
  width: 100%;
  height: 50vh;
  padding-top: 1rem;
}

.result-header {
  color: #f1f3f5;
  padding-left: 24px;
  padding-bottom: 24px;
}

.result-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  row-gap: 24px;
  justify-items: center;
  position: relative;
}

.display {
  position: fixed;
  width: 80%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  background-color: #868e96;
}

.display:hover {
  cursor: pointer;
}

@media (max-width: 820px) {
  .result-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media (max-width: 576px) {
  .result-list {
    grid-template-columns: repeat(2, 1fr);
  }
}
