.category-container {
  margin-top: 0;
  background-color: #212529;
}

.category-header {
  color: #f1f3f5;
  margin: 0;
  padding-top: 24px;
}

.category-movies-container {
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  gap: 18px;
  padding-top: 54px;
  padding-bottom: 24px;
}

.category-movies-container::-webkit-scrollbar {
  height: 8px;
  background-color: #343a40;
  border: 0.2px #212529 solid;
  border-radius: 8px;
}

.category-movies-container::-webkit-scrollbar-thumb {
  background-color: #495057;
  border-radius: 8px;
}
