.banner {
  width: 100%;
  height: 50vh;
  padding-top: 96px;
  color: rgba(163, 158, 158, 0.4);
}

.banner-name {
  font-size: 42px;
  letter-spacing: 0.5px;
}

.btn {
  color: #f1f3f5;
  padding: 12px 28px;
  background-color: #868e96;
  opacity: 0.9;
  border: 1px #f1f3f5 solid;
  border-radius: 8px;
  letter-spacing: 0.7px;
  font-size: 14px;
  display: inline-block;
  text-wrap: nowrap;

  /* Button sẽ nổi lên khi hover */
  transform: perspective(100px) translateZ(0px);
  transition: transform 100ms linear;
}

.btn:hover {
  /* Button sẽ nổi lên khi hover */
  transform: perspective(100px) translateZ(5px);
  cursor: pointer;
}

.btn-my-list {
  margin-left: 12px;
}

.banner-overview {
  width: 75%;
  line-height: 24px;
}
